import axios from "axios";
import React from "react";
import { useHistory, useParams } from "react-router-dom";

export default function NewsDetail() {
  const [artikel, setArtikel] = React.useState()
  let {slug} = useParams()
  let history = useHistory()
  const getArtikelDetail = async() =>{
    const url = `https://api-react-2.herokuapp.com/api/artikel/${slug}`
    try{
      let respon  = await axios.get(url)
      console.log(respon.data.data)
      console.log("sukses")
      setArtikel(respon.data.data)
      let data = respon.data.data
      if(data === null){
        return history.push("/berita")
      }
    }catch(err){
      console.log(err)
    }
  };

  React.useEffect(()=>{
    getArtikelDetail()
  },[])
    // let params = useParams()
    let {id} = useParams()
    // console.log(params)
  return (
    <React.Fragment>
      <p>news detail</p>
      <p className="text-4xl font-semibold my-5">{artikel?.judul}</p>
      <p>{artikel?.artikel}</p>
      <img src={artikel?.thumbnail} alt="" />
      {/* <p>ini adalah {params.slug}</p> app baris 34 */}
    </React.Fragment>
  );
}
