import React from "react";


export default function Aside(props) {
  const HandleInputTagline = (e) => {
        e.preventDefault()
        return props.setTagline(e.target.value)
  };

  function HandleChange(e){
    if(props.tagline === ""){
        e.preventDefault()
        return props.setTagline("info smk")
    }
  }
  return (
    <aside className="w-full">
      <input
        placeholder="tagline"
        className="border mt-5 w-full px-2 py-3"
        type="text"
        value={props.tagline}
        name=""
        id=""
        onInput={HandleInputTagline}
        onBlur={HandleChange}
      />
    </aside>
  );
}

