import React from "react";

export default function Isi({name, username, email, jenis_kelamin }) {
  return (
    <React.Fragment>
      <tr>
        <td className="border">{name}</td>
        <td className="border px-10 py-5">{username}</td>
        <td className="border">{jenis_kelamin}</td>
        <td className="border px-5">{email}</td>
      </tr>
    </React.Fragment>
  );
}
