import React from "react";
import Header from "./component/header";
import NewsDetail from "./pages/newsDetail";
// import Artikel from "./component/artikel";
// import Aside from "./component/aside";
// import Aside2 from "./component/aside2";
import About from "./pages/about";
import Contact from "./pages/contact";
import Home from "./pages/home";
import {
  BrowserRouter,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Post from "./pages/post";
import LatihanPost from "./pages/latihanPost";
import Update from "./pages/update";
import User from "./pages/post/user";

function App() {
  

  const [tagline, setTagline] = React.useState("info smk")
  const [header, setHeader] = React.useState("MQNEWS")


  return (
    <BrowserRouter>
      <div className="antialiased h-screen w-full bg-gray-50 px-16">
        <div className="h-20 w-full">
          <Header header={header} tagline={tagline}></Header>
        </div>
        
        <Switch>
          <Route path="/berita" exact><Home tagline={tagline} setHeader={setHeader} header={header} setTagline={setTagline}></Home></Route>
          <Route path="/berita/:slug" exact>
            <NewsDetail></NewsDetail>
          </Route>
          <Route path="/berita/update/:slug">
            <Update></Update>
          </Route>
          <Route path="/about">
            <About></About>
          </Route>
          <Route path="/contact">
            <Contact></Contact>
          </Route>
          <Route path="/post-artikel">
            <Post></Post>
          </Route>
          <Route path="/latihan-post">
            <LatihanPost></LatihanPost>
          </Route>
          <Route path="/daftar-user">
            <User></User>
          </Route>
          <Redirect from="*" to="/berita" />
        </Switch>
      </div>
    </BrowserRouter>
  );
}

export default App;
