import axios from "axios";
import React from "react";
import { useHistory, useParams } from "react-router";
import swal from "sweetalert";

export default function Update() {
  let history = useHistory();
  const [loading, setLoading] = React.useState(false);
  const [imagePreview, setImagePreview] = React.useState("");
  const [disableSubmit, setDisableSubmit] = React.useState(false);
  const [values, setValues] = React.useState({
    judul: "",
    artikel: "",
    thumbnail: "",
    user_id: "2",
  });

  const [error, setError] = React.useState({
    judul: "",
    artikel: "",
    thumbnail: "",
    user_id: "2",
  });
  let { slug } = useParams();
  console.log({ slug });
  const [artikel, setArtikel] = React.useState();
  const getDetail = async () => {
    let url = `https://api-react-2.herokuapp.com/api/artikel/${slug}`;
    try {
      let respon = await axios.get(url)
      setValues((values)=>({
          ...values,
          judul:respon.data.data.judul,
          artikel:respon.data.data.artikel,
          thumbnail:respon.data.data.thumbnail,
          user_id:respon.data.data.user_id,
          slug:respon.data.data.slug,
          id:respon.data.data.id,
          
        }))
        setImagePreview(respon.data.data.thumbnail)
    } catch (err) {
        console.log("ada masalah",err)
    }
  };
  const handleChange = (e) => {
    const key = e.target.id;
    const value = e.target.value;
    setValues((values) => ({
      ...values,
      [key]: value,
    }));
    if (key === "judul")
      if (value === "") {
        error.judul = "Judul Tidak Boleh Kosong";
      } else {
        error.judul = "";
      }
    if (key === "artikel")
      if (value === "") {
        error.artikel = "Artikel Tidak Boleh Kosong";
      } else if (value.replace(/\s/g, "").length < 10) {
        error.artikel = "Isi Artikel terlalu Sedikit";
      } else {
        error.artikel = "";
      }
   
    disableSubmitHandle();
  };
  const disableSubmitHandle = () => {
    if (
      error.judul === "" &&
      error.artikel === "" &&
      error.thumbnail === ""
    ) {
      return setDisableSubmit(false);
    } else {
      return setDisableSubmit(true);
    }
  };
  const handelUpload = (e) => {
    e.preventDefault();
    let reader = new FileReader();

    setValues((values) => ({
      ...values,
      thumbnail: e.target.files[0],
    }));
    reader.onloadend = () => {
      if (reader.readyState === 2) {
        setImagePreview(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };
  console.log(values);
  const handleSubmit = async (e) => {
    e.preventDefault();
    const url = `https://api-react-2.herokuapp.com/api/artikel/${values.id}`;
    console.log(url)
    const formData = new FormData();
    formData.append("judul", values.judul);
    formData.append("artikel", values.artikel);
    formData.append("thumbnail", values.thumbnail);
    formData.append("user_id", values.user_id);
    formData.append("_method", "put")
    setLoading(true);
    try {
      let result = await axios.post(url, formData, {
        headers: {
          "Content-Type": "multipart/form-data ",
        },
      });
      swal("Berhasil", "Data berhasil diupdate", "success");
      console.log(result);
      setLoading(false);
      history.push("/berita");
    } catch (error) {
      swal("Ups", "Terjadi kesalahan saat menyimpan", "error");
      setLoading(false);
      console.log("error");
    }
  };
  React.useEffect(() => {
    getDetail();
  }, []);
  return (
    <React.Fragment>
      <div className="font-sans">
        <h1 className="text-3xl font-bold "> POST ARTIKEL BARU </h1>
        <form
          autoComplete="off"
          onSubmit={handleSubmit}
          className="mt-5 grid gap-4"
        >
          <div className="grid gap-1">
            <label className="uppercase" htmlFor="judul">
              Judul Artikel
            </label>
            <input
              className="border py-2 px-5 rounded-lg"
              type="text"
              onChange={handleChange}
              onBlur={handleChange}
              name="judul"
              value={values.judul}
              id="judul"
            />
          </div>
          <p className="text-red-600 italic">{error.judul}</p>
          <div className="grid gap-1">
            <label className="uppercase" htmlFor="artikel">
              Isi Artikel
            </label>
            <textarea
              className="border py-2 px-5 rounded-lg"
              name="artikel"
              id="artikel"
              cols="30"
              rows="10"
              onChange={handleChange}
              onBlur={handleChange}
              value={values.artikel}
            ></textarea>
          </div>
          <p className="text-red-600 italic">{error.artikel}</p>
          <div>
            <img className="h-48" src={imagePreview} alt="" />
          </div>
          <div className="grid gap-1">
            <label className="uppercase" htmlFor="thumbnail">
              Thumbnail
            </label>
            <input
              className="border px-5 py-2 rounded-lg "
              type="file"
              name="thumbnail"
              onChange={handelUpload}
              id="thumbnail"
            />
          </div>
          <button
            disabled={disableSubmit}
            type="submit"
            className={`p-3 bg-red-600 w-full ${
              disableSubmit ? "bg-red-600" : "bg-green-500"
            }  flex justify-center items-center font-bold text-white uppercase text-2xl mb-10 rounded-lg`}
          >
            {loading ? "menyimpan" : "simpan"}
          </button>
        </form>
      </div>
    </React.Fragment>
  );
}
