import axios from "axios";
import React from "react";
import Isi from "./isiUser";

export default function User() {
    const [details, setDetails] = React.useState([]);
    const [isLoading, setIsLoading] = React.useState(false);
    const getArtikels = async () => {
      setIsLoading(true);
      const url = `http://belajar-react.smkmadinatulquran.sch.id/api/users/100`;
      try {
        let response = await axios.get(url);
        console.log(response.data.data);
        setDetails(response.data.data);
        setIsLoading(false);
      } catch (error) {
        console.log("error");
        setIsLoading(false);
      }
    };
    React.useEffect(() => {
      getArtikels();
    }, []);
  return (
    <React.Fragment>
      <div className=" py-10">
        <table className="table-fixed border">
          <thead className="">
            <tr className="">
              <th className="border-2 border-black w-1/4">name</th>
              <th className="border-2 border-black w-1/4">username</th>
              <th className="border-2 border-black w-1/2">jenis kelamin</th>
              <th className="border-2 border-black w-1/4">email</th>
            </tr>
          </thead>
          <tbody className="text-center">
          {details.map((detail, index) => (
              <Isi
              key={index}
                username={detail.username}
                name={detail.name}
                email={detail.email}
                jenis_kelamin={detail.jenis_kelamin}
                id={detail.id}
              ></Isi>
            ))}
          </tbody>
        </table>
      </div>
    </React.Fragment>
  );
}
