import React from "react";

export default function Aside2(props){
    function HandleChange(e){
        e.preventDefault()
        return props.setHeader(e.target.value)
    }
    function HandleBlur(e){
        if(props.header === ""){
            return props.setHeader("MQNEWS")
        }
    }
    // console.log(props)
    return(
        <aside className="w-full">
        <input
            placeholder="header"
            className="border mt-5 w-full px-2 py-3"
            type="text"
            value={props.header}
            name=""
            id=""
            onInput={HandleChange}
            onBlur={HandleBlur}
        />
        </aside>

    )
}