import axios from "axios";
import React from "react";
import Artikel from "../../component/artikel";
import Aside from "../../component/aside";
import Aside2 from "../../component/aside2";
import Loading from "../../component/loading";
export default function Home({tagline, setTagline, header, setHeader}) {
  const [artikels, setArtikels] = React.useState([])
  const [isLoading, setIsLoading] = React.useState(false)

  const getArtikel = async() => {
    setIsLoading(true)
    try{
      const url = `https://api-react-2.herokuapp.com/api/artikel?accept=application/json`
      let respon = await axios.get(url)
      console.log(respon.data)
      console.log("sukses")
      setArtikels(respon.data.data.data)
      setIsLoading(false)
    }catch(err){
      console.log(err)
      setIsLoading(false)
    }
  };

  React.useEffect(()=>{
    getArtikel()
  },[])
//   const [tagline, setTagline] = React.useState("info smk");

  return (
    <React.Fragment>
      <div className="grid grid-cols-6 ">
        <div className="col-span-5 grid grid-cols-5 gap-5 overflow-auto px-1 py-5">
          {isLoading? (<React.Fragment>
            <Loading></Loading>
            <Loading></Loading>
            <Loading></Loading>
          </React.Fragment>):(
            // <React.Fragment>
              artikels.map((artikel, index) => (
              <Artikel
                key={index}
                judul={artikel.judul}
                thumbnail={artikel.thumbnail}
                alt={artikel.alt}
                konten={artikel.artikel}
                slug={artikel.slug}
                id={artikel.id}
                getArtikel={getArtikel}
              ></Artikel>
            ))
            // </React.Fragment>

          )}

        </div>
        <div className="col-span-1">
          <Aside2 header={header} setHeader={setHeader}></Aside2>
          <Aside tagline={tagline} setTagline={setTagline}></Aside>
        </div>
      </div>
    </React.Fragment>
  );
}
