
import axios from "axios";
import React from "react";
import swal from "sweetalert";

export default function LatihanPost() {
  const [disableSubmit, setDisableSubmit] = React.useState(true);
  const [isLoading, setIsLoading] = React.useState(false);
  const [values, setValues] = React.useState({
    username: "",
    name: "",
    jenis_kelamin: "",
    email: "",
    password: "",
    password_confirmation: "",
  });
  const [error, setError] = React.useState({
    username: "",
    name: "",
    jenis_kelamin: "",
    email: "",
    password: "",
    password_confirmation: "",
  });

  const handleChange = (e) => {
    const key = e.target.name;
    const value = e.target.value;
    setValues((values) => ({
      ...values,
      [key]: value,
    }));
    if (key === "username")
      if (value === "") {
        error.username = "Username Tidak Boleh Kosong";
      } else {
        error.username = "";
      }
    if (key === "name")
      if (value === "") {
        error.name = " Nama Tidak Boleh Kosong";
        // } else if(value.replace(/\s/g, "").length < 10 || value.replace(/\s/g, "").length > 10){
      } else {
        error.name = "";
      }

      if (key === "jenis_kelamin")
      if (value === "") {
        error.jenis_kelamin= " Nama Tidak Boleh Kosong";
        // } else if(value.replace(/\s/g, "").length < 10 || value.replace(/\s/g, "").length > 10){
      } else {
        error.jenis_kelamin = "";
      }
    if (key === "email") {
      if (value === "") {
        error.email = " Anda Harus Memasukan Email";
        // } else if(value.replace(/\s/g, "").length < 10 || value.replace(/\s/g, "").length > 10){
      } else if (value.indexOf("@") === -1 || value.indexOf(".") === -1 ) {
        error.email = "Harap Masukan Email Dengan Benar ";
      } else {
        error.email = "";
      }
    }
    if (key === "password") {
      if (value === "") {
        error.password = "Password Tidak Boleh Kosong";
      } else if (value.replace(/\s/g, "").length < 8) {
        error.password = "Password Minimal 8 Karakter";
      } else {
        error.password = "";
      }
    }
    if (key === "password_confirmation") {
      if (value === "") {
        error.password_confirmation = "Konfirmasi Password Tidak Boleh Kosong";
      } else if (value !== values.password) {
        error.password_confirmation = "Password tidak sama";
      } else {
        error.password_confirmation = "";
      }
    }
    disableSubmitHandle();
  };
  const disableSubmitHandle = () => {
    console.log(values)
    if (
      values.username === "" &&
      values.name === "" &&
      values.jenis_kelamin === "" &&
      values.email === "" &&
      values.password === "" &&
      values.password_confirmation === ""
    ) {
      return setDisableSubmit(false);
    } else {
      return setDisableSubmit(true);
    }
  };
  
  console.log(values);
  const handleSubmit = async (e) => {
    e.preventDefault();
   
    const url = `http://belajar-react.smkmadinatulquran.sch.id/api/register`;
    const formData = new FormData();
    formData.append("username", values.username);
    formData.append("name", values.name);
    formData.append("jenis_kelamin", values.jenis_kelamin);
    formData.append("email", values.email);
    formData.append("password", values.password);
    formData.append("password_confirmation", values.password_confirmation);
    setIsLoading(true);
    try {
        let result = await axios.post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data ",
          },
        });
        console.log(result);
        setIsLoading(false);
        swal("Good job!", "You clicked the button!", "success");
      } catch (error) {
        console.log("error");
        setIsLoading(false);
        swal("Good job!", "You clicked the button! but something is Error", "error");
      }
    };
  
    return (
      <React.Fragment>
        <div className="font-sans border-2  px-80">
          <h1 className="text-3xl font-bold text-center py-5">LATIHAN FORM</h1>
          <form onSubmit={handleSubmit} className="mt-5 grid gap-4">
            <div className="grid gap-2">
              <label className='text-xl' htmlFor="username">Username</label>
              <input
                className="border py-2 px-5 rounded-lg"
                type="text"
                name="username"
                id="username"
                onBlur={handleChange}
                onChange={handleChange}
              />
            </div>
            <p className="text-red-600 italic">{error.username}</p>
            <div className="grid gap-2">
              <label className='text-xl' htmlFor="name">Nama</label>
              <input
                className="border py-2 px-5 rounded-lg"
                type="text"
                name="name"
                id="name"
                onBlur={handleChange}
                onChange={handleChange}
              />
            </div>
            <p className="text-red-600 italic">{error.name}</p>
            <div className="grid gap-2">
              <h1>Jenis Kelamin</h1>
              <div className="flex items-center">
                <label className='text-xl' htmlFor="laki_laki">Laki-Laki</label>
                <input
                  className="ml-2"
                  type="radio"
                  value="Laki-Laki"
                  name="jenis_kelamin"
                  id="laki_laki"
                  checked = {values.jenis_kelamin === "Laki-Laki"}
                  onBlur={handleChange}
                onChange={handleChange}
                  
                />
                <label className="ml-6 text-xl" htmlFor="perempuan">
                  Perempuan
                </label>
                <input
                  value="Perempuan"
                  className="ml-2"
                  type="radio"
                  name="jenis_kelamin"
                  id="perempuan"
                  checked = {values.jenis_kelamin === "Perempuan"}
                  onBlur={handleChange}
                onChange={handleChange}
                />
              </div>
            </div>
            <p className="text-red-600 italic">{error.jenis_kelamin}</p>
            <div className="grid gap-2">
              <label className='text-xl' htmlFor="email">Email</label>
              <input
                className="border py-2 px-5 rounded-lg"
                type="email"
                name="email"
                id="email"
                onBlur={handleChange}
                onChange={handleChange}
              />
            </div>
            <p className="text-red-600 italic">{error.email}</p>
            <div className="grid gap-2">
              <label className='text-xl' htmlFor="password">Password</label>
              <input
                className="border py-2 px-5 rounded-lg"
                type="password"
                name="password"
                id="password"
                onBlur={handleChange}
                onChange={handleChange}
              />
            </div>
            <p className="text-red-600 italic">{error.password}</p>
            <div className="grid gap-2">
              <label htmlFor="password-confirmation">Konfirmasi Password</label>
              <input
                className="border py-2 px-5 rounded-lg"
                type="password"
                name="password_confirmation"
                id="password_confirmation"
                onBlur={handleChange}
                onChange={handleChange}
              />
            </div>
            <p className="text-red-600 italic">{error.password_confirmation}</p>
            <button
            //   disabled={disableSubmit}
              type="submit"
            className={`${disableSubmit ?'bg-red-700' : 'bg-green-400'}  p-3 w-full  flex justify-center items-center font-bold text-white uppercase text-2xl mb-10 rounded-lg `}
          >
            {isLoading ? "Mengirim..." : "Kirim"}
          </button>
        </form>
      </div>
    </React.Fragment>
  );
}